import { useRouter } from 'next/router';

const HomePage: React.FC = () => {
  const router = useRouter();
  const { username } = router.query; // Access 'username' from the URL

  // Ensure that 'username' is a string
  const validUsername = typeof username === 'string' ? username : '';

  const { bg, textColor, navBgColor } = useColorModeStyles();

  return (
    <Grid
      templateAreas={{
        base: `"header"
               "main"`,
        md: `"header header"
             "nav main"`,
      }}
      gridTemplateRows={{ base: "auto 1fr", md: "50px 1fr" }}
      gridTemplateColumns={{ base: "1fr", md: "50px 1fr" }}
      height="100vh"
      gap="0.5"
      color={textColor}
      fontWeight="bold"
    >
      {/* Header Section */}
      <GridItem area="header" bg={bg} p={4}>
        <Header />
      </GridItem>

      {/* Sidebar Section */}
      <GridItem
        area="nav"
        bg={navBgColor}
        display={{ base: "none", md: "block" }}
      >
        <Sidebar />
      </GridItem>

      {/* Main Content Section */}
      <GridItem area="main" pl="2" bg={navBgColor}>
        {/* Pass valid username to MainProfile */}
        <MainProfile username={validUsername} />
      </GridItem>
    </Grid>
  );
};

export default HomePage;
